@media (max-width: 767px) {
    .album-card {
        max-width: 90%;
        max-height: 80%;
        min-height: 40%;
    }
}

.album-card {
    background-color: #2C394B;
    border: 7.5px solid #283344;
    border-radius: 5%;
    max-width: 20%;
    max-height: 60%;
    height: 40%;
    padding: 5px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.card-top {
    display: flex;
    width: 100%;
    height: 60%;
}

.card-bottom {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 30%;
    width: 100%;
}

.album-cover-image {
    /* Center Horizomtally */
    display: block;
    margin-right: auto;

    border-radius: 10px;

    max-width: 100%;
    height: auto;
}

.card-top-column {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
    width: 50%;
    /* border: 2px solid red; */
}


.ratings-container {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #6DAEDB;
    margin-left: auto;
    width: 90%;
    height: 45%;
    border-radius: 15px;
}

.play-now-container {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #1F2041;
    margin-left: auto;
    width: 90%;
    height: 45%;
    border-radius: 15px;
}

.spotify-logo-link {
    display: flex;
    max-width: 90%;
}

.spotify-logo {
    max-width: 100%;
}

.rating-text {
    color: white;
    text-align: center;
    font-family: 'Lilita One';
    /* font-weight: bolder; */
    font-size: 4em;


}

.album-title-text {
    /* Center Horizomtally */
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    /* Text Styling */
    color: white;
    font-family: monospace;
    font-weight: bold;
    /* Hide Overlowing text */
    font-size: min(1.5em, 1.2vw);

}

.album-artist-text {
    /* Center Horizomtally */
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    /* Text Styling */
    color: white;
    font-family: monospace;
    font-size: min(1.5em, 1vw);
}